.advantage {
  padding-top: 150px;
  padding-bottom: 150px;

  @include desktop {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @include tablet {
    padding-top: 80px;
    padding-bottom: 80px;
  }

  &__wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;

    @include tablet {
      flex-direction: column;
      gap: 35px;
    }
  }

  &__list {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
    gap: 20px;

    @include small-tablet {
      flex-wrap: wrap;
    }
  }

  &__item {
    width: 33.33%;

    @include small-tablet {
      width: 100%;
    }
  }

  &__decr {
    margin-bottom: 20px;
  }

  &__num {
    margin-right: 10px;
    font-weight: 600;
    font-size: 100px;
    line-height: 100%;
    letter-spacing: -0.02em;
    color: var(--siniy);

    @include big-desktop {
      font-size: 60px;
    }

    @include tablet {
      font-size: 50px;
    }
  }

  &__text-big {
    font-weight: 400;
    font-size: 30px;
    line-height: 100%;
    letter-spacing: -0.02em;
    color: var(--accent-2);

    @include big-desktop {
      font-size: 24px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--accent-2);

    @include mobile {
      font-size: 14px;
    }
  }
}
