.footer {
  padding: 68px 0 43px;
  background: #04112f;

  &__wrapper {
    display: flex;
    justify-content: flex-start;

    @include desktop {
      flex-direction: column;
      gap: 30px;
    }

    &:first-child {
      margin-bottom: 98px;

      @include desktop {
        margin-bottom: 50px;
      }
    }

    &:nth-child(2) {
      margin-bottom: 42px;
      border-bottom: 1px solid var(--linii-ne-osnovnoy-fon);
      padding-bottom: 50px;

      @include small-tablet {
        padding-bottom: 0;
      }
    }

    .col-width {
      @include desktop {
        flex-shrink: 2;
        width: 100%;
        max-width: 100%;
      }
    }
  }

  &__logo {
    width: 308px;

    @include desktop {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      width: 100%;
      gap: 50px;
    }

    @include small-tablet {
      flex-direction: column;
      align-items: flex-start;
      order: -1;
    }
  }

  &__img {
    @include small-tablet {
      width: 230px;
    }
  }

  &__text {
    margin-top: 35px;
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include desktop {
      margin-top: 0;
    }
  }

  &__inner {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    width: 100%;
  }

  &__lists {
    @include small-tablet {
      flex-direction: column;
      gap: 20px;
    }
  }

  &__list {}

  &__item {
    &:not(:last-child) {
      margin-bottom: 12px;
    }

    &:first-child {
      margin-bottom: 20px;
    }

    &:first-child .footer__link {
      margin-bottom: 20px;
      font-weight: 600;
      font-size: 20px;
      line-height: 120%;
      letter-spacing: -0.02em;
      color: var(--belyy);

      @include tablet {
        font-size: 18px;
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: rgba(255, 255, 255, 0.65);

    @include tablet {
      font-size: 14px;
    }
  }

  &__contact {}

  &__contact-name {
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 15px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: rgba(255, 255, 255, 0.65);
  }

  &__contact-link {
    font-weight: 600;
    font-size: 24px;
    line-height: 120%;
    letter-spacing: -0.02em;
    text-shadow: 0 1px 2px 0 rgba(255, 255, 255, 0.25);
    color: var(--belyy);

    @include desktop {
      font-size: 18px;
    }

    @include tablet {
      font-size: 14px;
    }
  }

  &__bootom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 15px;
  }

  &__bottom-text {
    font-weight: 400;
    font-size: 14px;
    color: rgba(255, 255, 255, 0.5);
  }
}

.social {
  @include desktop {
    flex-shrink: 2;
    justify-items: flex-end;
  }

  @include small-tablet {
    justify-items: flex-start;
  }

  &__name {
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 15px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: rgba(255, 255, 255, 0.65);
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 12px;
  }

  &__item {
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    padding: 10px;

    @include desktop {
      padding: 8px;
    }
  }

  &__link {
    display: block;

    @include desktop {
      width: 18px;
      height: 18px;
    }
  }

  &__pic {
    width: 24px;
    height: 24px;
    fill: var(--belyy);

    @include desktop {
      width: 18px;
      height: 18px;
    }
  }

  &__link--vb {}

  &__link--wa {}

  &__link--tg {}
}

.footer-top {
  @include desktop {
    display: flex;
    justify-content: space-between;
  }

  @include small-tablet {
    flex-direction: column;
    gap: 35px;
  }
}

.footer-cont {
  @include small-tablet {
    flex-direction: column;
    gap: 20px;
  }
}
