.participants {
  padding-bottom: 150px;

  @include desktop {
    padding-bottom: 100px;
  }

  @include tablet {
    padding-bottom: 80px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    width: 100%;
    gap: 20px;
  }

  &__item {
    grid-column: 3 span;

    @include small-tablet {
    grid-column: 6 span;
    }
  }

  &__img {
    margin-bottom: 25px;
  }

  &__text {
    font-weight: 400;
    font-size: 22px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include small-tablet {
      font-size: 16px;
    }
  }
}
