@mixin tabs {
  .tabs__nav-btn--active {
    background-color: var(--siniy);

    .tabs__nav-text-big {
      color: var(--belyy);
    }

    .tabs__nav-text-small {
      color: var(--belyy);
    }
  }

  .tabs__panel {
    display: none;
  }

  .tabs__panel--active {
    display: flex;
    justify-content: space-between;
  }
}
