.trust {
  padding-bottom: 150px;

  @include desktop {
    padding-bottom: 100px;
  }

  @include tablet {
    padding-bottom: 80px;
  }

 &__title {
    margin: 0;
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include tablet {
      width: 100%;
      font-size: 24px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--seryy);

    @include tablet {
      font-size: 16px;
    }
  }
}

.ticker {
  position: relative;
  overflow: hidden;
  background-color: #fff;
}

.ticker__wrapper {
  display: flex;

  @include tablet {
    gap: 30px;
  }
}

.ticker__item {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  animation: ticker 50s linear infinite;

  @include tablet {
    gap: 30px;
  }
}

.ticker__img {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  width: 385px;

  @include tablet {
    width: 200px;
  }
}

@keyframes ticker {
  0% {
    transform: translateZ(0);
  }

  100% {
    transform: translate3d(-100%, 0, 0);
  }
}
