.production {
  padding-bottom: 150px;

  @include desktop {
    padding-bottom: 100px;
  }

  @include tablet {
    padding-bottom: 80px;
  }

  &__wrapper {
    display: flex;
    align-items: flex-start;
    margin-bottom: 55px;

    @include tablet {
      flex-direction: column;
      gap: 35px;
    }
  }

  &__text {
    margin: 0;
    width: 100%;
    max-width: 705px;
    font-weight: 400;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include tablet {
      max-width: 100%;
      font-size: 24px;
    }
  }

  &__tabs {
    display: flex;
  }
}

.tabs {
  @include tabs;

  &__nav {
    @include tablet {
      display: none;
    }
  }

  &__nav-item {}

  &__nav-btn {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 21px 35px;
    width: calc(100% - 100px);

    @include tablet {
      padding: 0;
      width: 100%;
      text-align: left;
    }

    &::after {
      content: "";
      position: absolute;
      right: 36px;
      top: 50%;
      display: block;
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-radius: 61px;
      width: 25px;
      height: 25px;
      background-image: url("../img/arr-btn.svg");
      background-position: center;
      background-size: 7px 11px;
      background-repeat: no-repeat;
      transform: translateY(-50%);
    }
  }

  &__nav-text-big {
    margin-bottom: 5px;
    font-weight: 600;
    font-size: 18px;
    line-height: 160%;
    color: var(--chernyy);

    @include tablet {
      font-weight: 600;
      font-size: 24px;
      line-height: 160%;
      color: var(--chernyy);
    }

    @include mobile {
      font-size: 18px;
    }
  }

  &__nav-text-small {
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    letter-spacing: -0.01em;
    color: var(--seryy);

    @include tablet {
      margin-bottom: 15px;
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      letter-spacing: -0.01em;
      color: var(--seryy);
    }
  }

  &__content {}

  &__panel {
    display: flex;
    justify-content: space-between;
  }

  &__col {
    width: 100%;
    max-width: 705px;
  }

  &__list {
    margin-bottom: 55px;
  }

  &__item {
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    letter-spacing: -0.01em;
    color: var(--seryy);

    @include mobile {
      font-size: 14px;
    }

    &:not(:last-child) {
      margin-bottom: 50px;

      @include tablet {
        margin-bottom: 25px;
      }

      @include mobile {
        margin-bottom: 15px;
      }
    }
  }

  &__descr {
    display: flex;
    flex-direction: column;
    gap: 5px;
  }

  &__descr-tablet {
    @include tablet {
      display: none;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    color: #000;

    @include tablet {
      font-size: 14px;
    }
  }

  &__text-size {
    font-size: 32px;

    @include tablet {
      font-size: 24px;
    }
  }

  &__img {
    width: 500px;

    @include big-desktop {
      width: 400px;
    }

    @include tablet {
      align-self: center;
      width: 100%;
      max-width: 400px;
    }
  }
}

.tabs .tabs__nav-btn--active {
  background-color: var(--siniy);

  @include tablet {
    background-color: transparent;
  }

  .tabs__nav-text-big {
    color: var(--belyy);

    @include tablet {
      font-weight: 600;
      font-size: 24px;
      line-height: 160%;
      color: var(--chernyy);
    }

    @include mobile {
      font-size: 18px;
    }
  }

  .tabs__nav-text-small {
    color: var(--belyy);

    @include tablet {
      font-weight: 400;
      font-size: 20px;
      line-height: 140%;
      letter-spacing: -0.01em;
      color: var(--seryy);
    }

    @include mobile {
      font-size: 14px;
    }
  }
}

.tabs .tabs__panel {
  @include tablet {
    display: flex;
  }
}
