.section-form {
  background: var(--siniy);

  &__col {
    position: relative;
  }

  &__container-max {
    position: relative;
  }

  &__photo {
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;

    @include tablet {
      display: none;
    }
  }

  &__inner {
    padding: 50px 0;
    width: 100%;
  }

  &__img {
    height: 100%;
  }

  &__descr {
    position: absolute;
    left: 0;
    bottom: 70px;
    display: flex;
    flex-direction: column;
  }

  &__name {
    font-weight: 600;
    font-size: 18px;
    line-height: 160%;
    color: var(--belyy);
  }

  &__position {
    font-weight: 600;
    font-size: 15px;
    line-height: 120%;
    color: var(--belyy);
    opacity: 0.6;
  }

  &__text {
    display: inline-block;
    margin-bottom: 10px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include mobile {
      font-size: 14px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 47px;
    font-weight: 400;
    font-size: 60px;
    line-height: 115%;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include desktop {
      font-size: 40px;
    }

    @include mobile {
      font-size: 30px;
    }
  }
}

.form {
  width: 100%;
  max-width: 450px;

  &__label {
    display: flex;
    flex-direction: column;
    margin-bottom: 26px;
    border-bottom: 1px solid var(--linii-ne-osnovnoy-fon);
  }

  &__text {
    margin-bottom: 11px;
    font-weight: 600;
    font-size: 15px;
    line-height: 120%;
    color: var(--belyy);
  }

  &__input {
    margin-bottom: 11px;
    background-color: transparent;

    &::placeholder {
      font-weight: 400;
      font-size: 18px;
      line-height: 160%;
      letter-spacing: -0.02em;
      color: var(--belyy);
      opacity: 0.6;
    }
  }

  &__btn {
    padding: 30px 35px;
    width: 100%;
    font-weight: 600;
    font-size: 18px;
    line-height: 160%;
    color: var(--siniy);
    background: var(--belyy);

    @include mobile {
      font-size: 13px;
    }
  }
}
