.benefit {
  padding-bottom: 150px;

  @include desktop {
    padding-bottom: 100px;
  }

  @include tablet {
    padding-bottom: 80px;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    width: 460px;
    font-weight: 400;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include tablet {
      width: 100%;
      font-size: 24px;
    }
  }

  &__wrapper-slider {
    position: relative;
  }
}


.benefit-slider {
  &__block {}

  &__image {
    margin-bottom: 30px;
    width: 100%;
    height: 325px;
  }

  &__descr {
    margin: 0;
    padding: 25px;
    font-weight: 400;
    font-size: 24px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include desktop {
      font-size: 20px;
    }
  }

  &__btns {
    position: absolute;
    right: 40px;
    top: -80px;
    z-index: 10;
    width: 100%;
    max-width: 120px;

    @include small-tablet {
      top: 105%;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 16px;
    height: 16px;
    color: #0c1c2f;

    &::after {
      display: none;
    }

    svg {
      display: block;
      width: 100%;
      height: 100%;
      color: inherit;
      fill: currentcolor;
    }

    &:focus {
      outline: 2px solid var(--color-primary);
      outline-offset: 2px;
    }

    &:hover {
      opacity: 1;
    }

    &:active {
      opacity: 1;
    }

    &.swiper-button-disabled {
      opacity: 0.4;
      pointer-events: none;
    }
  }

  &__prev {
    position: absolute;
    left: 0;
    z-index: 10;
  }

  &__next {
    position: absolute;
    right: 0;
    z-index: 10;
  }

  &__pag {
    color: #0c1c2f;
  }

  .swiper-pagination-bullet-active {
    background-color: #0c1c2f;

  }
}





