.about {
  margin-bottom: 150px;
  padding: 53px 0;
  background: var(--siniy);

  @include desktop {
    margin-bottom: 100px;
  }

  @include tablet {
    margin-bottom: 80px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    width: 100%;
    gap: 20px;
  }

  &__item {
    grid-column: 2 span;

    @include small-tablet {
      grid-column: 6 span;
    }
  }

  &__text {
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 30px;
    line-height: 110%;
    letter-spacing: -0.02em;
    color: var(--belyy);
  }

  &__text-big {
    font-weight: 600;
    font-size: 100px;
    line-height: 100%;

    @include big-desktop {
      font-size: 60px;
    }

    @include tablet {
      font-size: 50px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include tablet {
      font-size: 16px;
    }
  }
}
