// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "RF Dewi Extended", sans-serif;
  --content-width: 1680px;
  --content-width-min: 718px;
  --content-width-max: 1896px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --container-width-min: calc(var(--content-width-min) + (var(--container-offset) * 2));
  --container-width-max: calc(var(--content-width-max) + (var(--container-offset) * 2));

  // colors
  --chernyy: #010513;
  --belyy: #fff;
  --seryy: #435266;
  --siniy: #0071fe;
  --temno-seryy: #3d444c;
  --linii-ne-osnovnoy-fon: rgba(255, 255, 255, 0.15);
  --linii-osnovnoy-fon: #eee;
  --green: #07ac0d;
  --accent-2: #05173f;
  --text-bg: rgba(255, 255, 255, 0.8);
}
