.features {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 150px;
  height: 100%;
  min-height: 750px;
  background-image: url("../img/features-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include desktop {
    margin-bottom: 100px;
  }

  @include tablet {
    margin-bottom: 80px;
    padding: 50px 0 30px;
    min-height: 100%;
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    background-color: rgba(0, 0, 0, .6);
  }

  &__wrapper {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;

    @include tablet {
      flex-direction: column;
      gap: 35px;
    }
  }

  &__title {
    margin: 0;
    padding-right: 60px;
    font-weight: 400;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include tablet {
      font-size: 24px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 70px 10px;

    @include tablet {
      gap: 40px;
    }
  }

  &__item:nth-child(odd) {
    grid-column: 4 span;

    @include big-desktop {
      grid-column: 3 span;
    }

    @include mobile {
      grid-column: 6 span;
      padding: 30px 0;
    }
  }

  &__item:nth-child(even) {
    grid-column: 2 span;

    @include big-desktop {
      grid-column: 3 span;
    }

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__text {
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 50px;
    line-height: 110%;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include big-desktop {
      font-size: 35px;
    }

    @include tablet {
      font-size: 18px;
    }
  }

  &__text--big {
    font-size: 80px;

    @include big-desktop {
      font-size: 50px;
    }

    @include tablet {
      font-size: 45px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include tablet {
      font-size: 14px;
    }
  }
}
