.configuration {
  padding-bottom: 150px;

  @include desktop {
    padding-bottom: 100px;
  }

  @include tablet {
    padding-bottom: 80px;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    width: 450px;
    font-weight: 400;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include tablet {
      width: 100%;
      font-size: 24px;
    }

    @include small-tablet {
      margin-bottom: 0;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 10px 25px;

    @include tablet {
      grid-template-columns: repeat(6, 1fr);
    }
  }

  &__item {
    grid-column: 4 span;
    padding: 30px;

    @include tablet {
      grid-column: 3 span;
    }

    @include small-tablet {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      grid-column: 6 span;
      padding: 20px;
      gap: 25px;
    }
  }

  &__pic {
    width: 70px;
    height: 70px;
    fill: #0071FE;

    @include tablet {
      flex-shrink: 0;
      width: 30px;
      height: 30px;
    }
  }

  &__pic--cart {
    fill: var(--belyy);
    stroke: var(--siniy);
  }

  &__text {
    margin: 0;
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 24px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include tablet {
      margin-bottom: 0;
      font-size: 16px;
    }
  }
}
