.specifications {
  padding-bottom: 150px;

  @include desktop {
    padding-bottom: 100px;
  }

  @include tablet {
    padding-bottom: 80px;
  }

  &__col {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: -30px;
      width: 100%;
      height: 455px;
      max-width: 510px;
      background-image: url("../img/specifications-pic.png");
      background-size: cover;
      background-repeat: no-repeat;

      @include big-desktop {
        height: 370px;
      }

      @include tablet {
        display: none;
      }
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    width: 450px;
    font-weight: 400;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include tablet {
      width: 100%;
      font-size: 24px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--seryy);

    @include tablet {
      font-size: 16px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    row-gap: 70px;
  }

  &__item:nth-child(odd) {
    grid-column: 4 span;

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__item:nth-child(even) {
    grid-column: 2 span;

    @include mobile {
      grid-column: 6 span;
    }
  }

  &__item-text {
    margin-bottom: 25px;
    font-weight: 400;
    font-size: 43px;
    line-height: 110%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include big-desktop {
      margin-bottom: 10px;
      font-size: 30px;
    }

    @include tablet {
      font-size: 24px;
    }
  }

  &__text--big {
    font-size: 80px;

    @include big-desktop {
      font-size: 50px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--chernyy);
  }
}
