.finance {
  padding-bottom: 150px;

  @include desktop {
    padding-bottom: 100px;
  }

  @include tablet {
    padding-bottom: 80px;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include tablet {
      width: 100%;
      font-size: 24px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--seryy);

    @include tablet {
      font-size: 16px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 15px;
  }

  &__item {
    grid-column: 2 span;

    @include small-tablet {
      justify-items: center;
      grid-column: 6 span;
    }
  }

  &__image {
    margin-bottom: 25px;
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 22px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include tablet {
      font-size: 18px;
    }
  }
}
