.customers {
  padding-bottom: 150px;

  @include desktop {
    padding-bottom: 100px;
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    width: 100%;
    gap: 20px;

    @include small-tablet {
      display: flex;
    }
  }

  &__item {
    grid-column: 2 span;
  }

  &__img {
    margin-bottom: 30px;
  }

  &__text {
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--seryy);
  }

  .swiper-wrapper {
    @include small-tablet {
      display: flex;
      column-gap: 0;
    }
  }
}

.customers-slider {
  &__btns {
    display: none;

    @include small-tablet {
      position: absolute;
      right: 40px;
      top: 105%;
      z-index: 10;
      display: block;
      width: 100%;
      max-width: 120px;
    }
  }

  &__btn {
    @include small-tablet {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 16px;
      height: 16px;
      color: #0c1c2f;

      &::after {
        display: none;
      }

      svg {
        display: block;
        width: 100%;
        height: 100%;
        color: inherit;
        fill: currentcolor;
      }

      &:focus {
        outline: 2px solid var(--color-primary);
        outline-offset: 2px;
      }

      &:hover {
        opacity: 1;
      }

      &:active {
        opacity: 1;
      }

      &.swiper-button-disabled {
        opacity: 0.4;
        pointer-events: none;
      }
    }
  }

  &__prev {
    position: absolute;
    left: 0;
    z-index: 10;
  }

  &__next {
    position: absolute;
    right: 0;
    z-index: 10;
  }
}

.swiper {
  width: 100%;
}
