.certificate {
  padding-bottom: 150px;

  @include desktop {
    padding-bottom: 100px;
  }

  @include tablet {
    padding-bottom: 80px;
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 40px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--chernyy);

    @include tablet {
      width: 100%;
      font-size: 24px;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: 30px 25px;

    @include small-tablet {
      grid-template-columns: repeat(6, 1fr);
    }

  }

  &__item {
    grid-column: 4 span;
    overflow-y: hidden;
    border: 1px solid var(--linii-osnovnoy-fon);
    padding: 45px 35px 0;
    height: 501px;

    @include desktop {
      grid-column: 6 span;
    }
  }

  &__name {
    margin-bottom: 35px;
    font-weight: 600;
    font-size: 22px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: #0c1c2f;

    @include small-tablet {
      font-size: 16px;
    }
  }

  &__link {
    display: inline-block;
    margin-bottom: 35px;
    border: 1px solid rgba(0, 113, 254, 0.5);
    border-radius: 3px;
    padding: 22px 20px;
    font-weight: 600;
    font-size: 18px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--siniy);
  }

  &__img {
    // border: 1px solid rgba(0, 0, 0, 0.17);
    display: flex;
    justify-content: center;
  }
}
