*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

.main {
  counter-reset: title 0;
  position: relative;
}

.wrapper-line {
  position: absolute;
  left: 0;
  top: 0;
  border-left: 2px solid #000;
  width: 100%;
  max-width: 1896px;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  position: relative;
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.container-line {
  position: relative;
  padding: 0;

  &::after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    border-right: 1px solid var(--linii-ne-osnovnoy-fon);
    border-left: 1px solid var(--linii-ne-osnovnoy-fon);
    width: 100%;
    height: calc(100% + 450px);
    max-width: 1650px;
  }

  &::before {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;
    border-right: 1px solid var(--linii-ne-osnovnoy-fon);
    width: 100%;
    height: calc(100% + 450px);
    max-width: 547px;

    @include big-desktop {
      width: 400px;
    }

    @include tablet {
      display: none;
    }
  }

}

.container-min {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width-min);
}

.container-max {
  margin: 0 auto;
  max-width: var(--container-width-max);
}

.section-wrapper {
  display: flex;
  justify-content: flex-start;

  @include tablet {
    flex-direction: column;
    gap: 35px;
  }
}

.section-inner {
  width: 100%;
}

.section-block {
  margin-bottom: 50px;
  width: 100%;
  max-width: 700px;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section-title {
  counter-increment: title 1;
  position: relative;
  margin: 0;
  padding-left: 40px;
  font-weight: 400;
  font-size: 22px;
  line-height: 120%;
  letter-spacing: -0.02em;
  color: var(--chernyy);

  @include small-tablet {
    padding-left: 30px;
    font-size: 16px;
  }

  &::after {
    content: counter(title, decimal-leading-zero);
    position: absolute;
    left: 0;
    top: 0;
    font-weight: 400;
    font-size: 22px;
    line-height: 120%;
    letter-spacing: -0.02em;
    color: var(--siniy);

    @include small-tablet {
      font-size: 16px;
    }
  }
}

.section-title--color {
  color: var(--belyy);

  &::after {
    color: var(--belyy);
  }
}

.col-width {
  position: relative;
  flex-shrink: 0;
  width: 100%;
  max-width: 547px;

  @include big-desktop {
    width: 400px;
  }

  @include tablet {
    width: 100%;
  }
}

