.hero {
  position: relative;
  display: flex;
  align-items: flex-end;
  padding-bottom: 120px;
  height: 100%;
  min-height: 800px;
  background-image: url("../img/hero-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @include tablet {
    padding-bottom: 80px;
    min-height: 600px;
  }

  @include mobile {
    min-height: 700px;
  }

  &::before {
    content: "";
    position: absolute;
    inset: 0;
    background-color: rgba(0, 0, 0, .6);
  }

  &__index {
    &::before {
      z-index: 1;
      height: calc(100% + 275px);
    }

    &::after {
      z-index: 1;
      border-left: 1px solid transparent;
      height: calc(100% + 275px);
    }
  }

  &__wrapper {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background: rgba(27, 71, 99, 0.1);
  }

  &__title {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 50px;
    line-height: 115%;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include tablet {
      font-size: 32px;
    }
  }

  &__text {
    margin: 0;
    margin-bottom: 35px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include tablet {
      font-size: 16px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    gap: 15px;

    @include mobile {
      flex-direction: column;
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    padding: 30px 35px;
    font-weight: 400;
    font-size: 18px;
    line-height: 160%;
    letter-spacing: -0.02em;
    text-align: center;
    color: var(--belyy);
    background-color: var(--siniy);
    gap: 15px;

    @include tablet {
      padding: 25px;
      font-size: 16px;
    }

    @include mobile {
      justify-content: center;
      width: 100%;
    }

    &::after {
      content: "";
      display: block;
      border: 1px solid rgba(255, 255, 255, 0.1);
      border-radius: 61px;
      width: 25px;
      height: 25px;
      background-image: url("../img/arr-btn.svg");
      background-position: center;
      background-size: 7px 11px;
      background-repeat: no-repeat;
    }
  }

  &__btn--transparent {
    background: rgba(0, 113, 254, 0.6);

    &::after {
      content: "";
      transform: rotate(90deg);
    }
  }
}
