.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  border-bottom: 1px solid var(--linii-ne-osnovnoy-fon);
  padding: 33px 0;
  width: 100%;

  @include tablet {
    padding: 25px 0;
  }

  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 15px;
  }

  &__logo {
    @include desktop {
      width: 120px;
    }
  }

  &__social-contacts {
    display: flex;
    place-content:space-between;
    gap: 50px;

    @include mobile {
      flex-direction: column;
      align-items: flex-end;
      gap: 12px;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    @include mobile {
      flex-direction: row;
      gap: 12px;
    }
  }

  &__contacts-tel {
    font-weight: 600;
    font-size: 20px;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include desktop {
      font-size: 14px;
    }

    @include mobile {
      border: 1px solid rgba(255, 255, 255, 0.3);
      border-radius: 50%;
      padding: 8px;
    }
  }

  &__contacts-mail {
    font-weight: 400;
    font-size: 20px;
    letter-spacing: -0.02em;
    color: var(--belyy);

    @include desktop {
      font-size: 14px;
    }

    @include mobile {
      border: 1px solid rgba(255, 255, 255, 0.3);
      border-radius: 50%;
      padding: 8px;
    }
  }

  &__contacts-text {
    @include mobile {
      display: none;
    }
  }

  &__contacts-svg {
    display: none;
    width: 18px;
    height: 18px;
    fill: var(--belyy);

    @include mobile {
      display: block;
    }
  }
}

.raitings {
  display: flex;
  justify-content: space-between;
  gap: 25px;

  @include tablet {
    display: none;
  }

  &__block {}

  &__block-top {
    display: flex;
    justify-content: space-between;
    margin-bottom: 5px;
    gap: 15px;
  }

  &__sum {
    font-weight: 600;
    font-size: 20px;
    color: var(--belyy);

    @include desktop {
      font-size: 16px;
    }
  }

  &__stars {
    @include desktop {
      width: 100px;
    }
  }

  &__text {
    font-weight: 400;
    font-size: 12px;
    text-align: center;
    color: #fffafa;

    @include desktop {
      font-size: 10px;
    }
  }
}
